import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link, Redirect } from 'react-router-dom';

import { Row, Col, Card } from 'antd';

import { countryWithAreasSelector } from '../../store/otherSelectors';
import { fuelProducedOrConsumedCountrySelector } from '../../store/countries';

import { CountryType } from '../../utils/types';
import { isMobileOrTablet } from '../../utils';

import BasicChartContainer from './containers/BasicChartContainer';

import GeoJSONMap from '../../components/GeoJSONMap';
import ScrollToTop from '../../components/ScrollToTop';
import SummaryTab from './components/SummaryTab';
import IndependencyTab from './components/IndependencyTab';
import ClimateTab from './components/ClimateTab';

const tabList = [
  { key: 'summary', tab: 'Summary' },
  { key: 'independency', tab: 'Energy (in)dependency' },
  { key: 'climate', tab: 'Climate change' },
];

const IndependencyTabContainer = connect((state, props) => ({
  fuelProducedOrConsumed: fuelProducedOrConsumedCountrySelector(
    props.countryCode,
    state,
  ),
}))(IndependencyTab);

const tabContent = {
  summary: countryCode => <SummaryTab countryCode={countryCode} />,
  independency: countryCode => (
    <IndependencyTabContainer countryCode={countryCode} />
  ),
  climate: countryCode => <ClimateTab countryCode={countryCode} />,
};

function CountryView(props) {
  const { country, goTo, currentTab } = props;

  if (!country || country.disabled) {
    return <Redirect to="/" />;
  }

  const countryCode = country.alpha2Code;
  const flag = isMobileOrTablet() ? country.flagIcon : '';
  return (
    <div className="CountryView">
      <ScrollToTop countryCode={countryCode} />
      <Row gutter={16}>
        <Col xs={24} sm={24} md={18}>
          <Card title={<h2>{`${flag} ${country.commonName}`}</h2>}>
            <div>{`Capital: ${country.capital}`}</div>
            <div>{`Area: ${country.area.toLocaleString()} km`}</div>
            <div>
              {`Member of: `}
              {country.areas.map((area, i) => (
                <Link key={area.code} to={`/area/${area.code}`}>
                  {`${i > 0 ? ', ' : ''}${area.name}`}
                </Link>
              ))}
            </div>
            <Row style={{ marginTop: '20px' }} gutter={20}>
              <Col xs={24} sm={24} md={24} lg={12}>
                <BasicChartContainer
                  statisticCode="POPULATION"
                  countryCode={countryCode}
                  color="#2c82c9"
                />
              </Col>
              <Col xs={24} sm={24} md={24} lg={12}>
                <BasicChartContainer
                  statisticCode="GDP_2010_USD"
                  countryCode={countryCode}
                  color="#f22613"
                  perCapita
                />
              </Col>
            </Row>
          </Card>
        </Col>
        <Col xs={0} sm={0} md={6}>
          <img
            style={{
              width: '100%',
              marginBottom: '20px',
              maxHeight: '180px',
            }}
            src={`/img/flags/${country.alpha3Code.toLowerCase()}.svg`}
            alt={`Flag of ${country.commonName}`}
          />
          <GeoJSONMap geojson={country.geojson} center={country.latlng} />
        </Col>
      </Row>
      <Row>
        <Card
          key={currentTab}
          tabList={tabList}
          activeTabKey={currentTab}
          onTabChange={tab => goTo(`/country/${countryCode}/${tab}`)}
        >
          {tabContent[currentTab](countryCode)}
        </Card>
      </Row>
    </div>
  );
}

CountryView.propTypes = {
  currentTab: PropTypes.string,
  country: CountryType,
  goTo: PropTypes.func.isRequired,
};
CountryView.defaultProps = {
  currentTab: 'summary',
  country: null,
};

export default connect((state, props) => ({
  currentTab: props.match.params.tab,
  goTo: url => props.history.push(url),
  country: countryWithAreasSelector(props.match.params.countryCode, state),
}))(CountryView);
